'use client';
import { useEffect, useState } from 'react';
import InputField from '../../components/InputField';
import Button from '../../components/Button';

const UserProfile = () => {
    const [user, setUser] = useState(null);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    const [newPassword, setNewPassword] = useState('');
    const [confirmPassword, setConfirmPassword] = useState('');
    const [passwordResetSuccess, setPasswordResetSuccess] = useState(null);
    const [passwordResetError, setPasswordResetError] = useState(null);
    const [passwordLoading, setPasswordLoading] = useState(false);

    useEffect(() => {
        const fetchProfile = async () => {
            try {
                const response = await fetch('http://localhost:5000/profile', {
                    method: 'GET',
                    credentials: 'include', // Allow cookies to be sent
                    headers: { 'Content-Type': 'application/json' },
                });

                if (!response.ok)
                    throw new Error('Not logged in, Login first.');

                const data = await response.json();
                setUser(data);
            } catch (err) {
                setError(err.message);
            } finally {
                setLoading(false);
            }
        };

        fetchProfile();
    }, []);

    const handleResetPassword = async () => {
        setPasswordResetSuccess(null);
        setPasswordResetError(null);
        setPasswordLoading(true);

        if (newPassword !== confirmPassword) {
            setPasswordResetError('Passwords do not match');
            setPasswordLoading(false);
            return;
        }

        try {
            const response = await fetch(
                'http://localhost:5000/reset-password',
                {
                    method: 'POST',
                    headers: { 'Content-Type': 'application/json' },
                    credentials: 'include', // Ensures authentication token is sent
                    body: JSON.stringify({ newPassword }),
                }
            );

            const data = await response.json();
            if (!response.ok)
                throw new Error(data.message || 'Failed to reset password');

            setPasswordResetSuccess('Password reset successful!');
            setNewPassword('');
            setConfirmPassword('');
        } catch (err) {
            setPasswordResetError(err.message);
        } finally {
            setPasswordLoading(false);
        }
    };

    return (
        <div className="flex justify-center items-center min-h-screen bg-gray-100 p-4">
            <div className="bg-white shadow-lg rounded-lg p-6 w-full max-w-md text-center">
                {loading ? (
                    <p className="text-gray-500">Loading...</p>
                ) : error ? (
                    <div>
                        <p className="text-red-500">{error}</p>
                        <button
                            onClick={() => (window.location.href = '/login')}
                            className="mt-4 px-4 py-2 bg-blue-500 text-white rounded-md hover:bg-blue-600 transition">
                            Login
                        </button>
                    </div>
                ) : (
                    <>
                        <h2 className="text-2xl font-semibold mt-4">
                            {user.name}
                        </h2>
                        <p className="text-gray-600">{user.email}</p>

                        {/* Reset Password Section */}
                        <div className="mt-6">
                            <h3 className="text-lg font-semibold text-gray-800">
                                Reset Password
                            </h3>
                            {passwordResetSuccess && (
                                <p className="text-green-500">
                                    {passwordResetSuccess}
                                </p>
                            )}
                            {passwordResetError && (
                                <p className="text-red-500">
                                    {passwordResetError}
                                </p>
                            )}

                            <InputField
                                label="New Password"
                                type="password"
                                name="newPassword"
                                value={newPassword}
                                onChange={(e) => setNewPassword(e.target.value)}
                                placeholder="Enter new password"
                            />
                            <InputField
                                label="Confirm Password"
                                type="password"
                                name="confirmPassword"
                                value={confirmPassword}
                                onChange={(e) =>
                                    setConfirmPassword(e.target.value)
                                }
                                placeholder="Confirm new password"
                            />
                            <Button
                                text={
                                    passwordLoading
                                        ? 'Resetting...'
                                        : 'Reset Password'
                                }
                                onClick={handleResetPassword}
                                disabled={passwordLoading}
                            />
                        </div>

                        {/* Logout Button */}
                        <div className="mt-6">
                            <button
                                onClick={() => {
                                    fetch('http://localhost:5000/logout', {
                                        method: 'GET',
                                        credentials: 'include',
                                    })
                                        .then(() => {
                                            localStorage.removeItem('token');
                                            window.location.href = '/login';
                                        })
                                        .catch((err) => console.error(err));
                                }}
                                className="mt-4 px-4 py-2 bg-red-500 text-white rounded-md hover:bg-red-600 transition">
                                Logout
                            </button>
                        </div>
                    </>
                )}
            </div>
        </div>
    );
};

export default UserProfile;
